import React from 'react';
import styled from 'styled-components'
import Technology from './pages/Technology'
import Timeline from './pages/Timeline'


function App() {
    
    const handleLink = (e) => {
        e.preventDefault();
        var href = e.target.getAttribute('href');
        var section = document.querySelector(href);
        var rect = section.scrollIntoView({
            behavior: 'smooth'

        });
    }

    return (
        <Container>
                <Navbar className="navbar navbar-expand-lg navbar-dark bg-dark">
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                        <div className="navbar-nav">
                            <a className="nav-item nav-link" href="#home" onClick={e => handleLink(e)}>Home</a>
                            <a className="nav-item nav-link" href="#technology" onClick={e => handleLink(e)}>Technology</a>
                            <a className="nav-item nav-link" href="#about-me" onClick={e => handleLink(e)}>About me</a>
                            <a className="nav-item nav-link" href="#education" onClick={e => handleLink(e)}>Education</a>
                        </div>
                    </div>
                </Navbar>
            <HeaderSection id="home">
                <div className="header-title">

                    <h1>Portfolio</h1>
                </div>
            </HeaderSection>
            <ContentSection>
                {/* Technolofy Experence */}
                <Technology/>
                
                {/* Portfolio */}
                <PortfolioSection id="about-me">

                    <h2 className="section-title-header">About me</h2>

                    <div className="block-image">
                        <img src="./images/my-image.jpg" alt=""/>
                    </div>
                    <div className="block-content">
                        <p className="title-about text-primary">Teerach Mincharoen</p>
                        <hr/>
                        <p><b>Date of birth :</b> 28 September 1997</p>
                        <p><b>Age :</b> 22 years old</p>
                        <p><b>Personality :</b> Fast learner, Responsible, Friendly</p>
                        <p><b>Religion :</b> Islam</p>
                        <p><b>Height / Weight :</b> 175cm / 60kgs.</p>
                        <p><b>Nationality :</b> Thai</p>
                        <p><b>Health :</b> Excellence</p>
                        <p><b>Contact :</b> Tel 086-714-0083</p>
                        <p><b>Email :</b> teerach.min@gmail.com</p>
                    </div>


                </PortfolioSection>

                {/* Timeline */}
                <Timeline/>

            </ContentSection>
        </Container>
    );
}

    const HeaderSection = styled.section`
        height: 100vh;
        position:relative;
        display: flex;
        justify-content: center;
        color:#fff;
        align-items: center;
        background: fixed;
        background-image: url(./images/banner.jpg);
        background-size: cover;
        background-position: 20% 100%;   

        ::after {
            content: "";
            background: #fff;
            height: 200px;
            -webkit-transform: skewY(-5deg);
            -ms-transform: skewY(-5deg);
            transform: skewY(-5deg);
            position: absolute;
            left: 0;
            bottom: -100px;
            right: 0;
            z-index: 1;

        }

        ::before {
            position: absolute;
            content: "";
            height:auto;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: #9E9E9E;
            opacity:0.6;
            z-index: 1;
            
        }

        .header-title{
            z-index:2;
        }
    `

    const Navbar = styled.section`
        // height: 60px;
        width:100%;
        z-index:999;
        position:fixed;
        top:0;
        left:0;
        background: #2e2e2e !important;
        
        .navbar-collapse {
            justify-content: center;
        }

        .nav-link {
            padding-left: 1.5rem !important;
            padding-right: 1.5rem !important;
        }

        .nav-item {
            color: #fff;
        }
    `

    const Container = styled.section`
        color : #575757;
        p {
            font-size: 14px;
        }
        .section-title-sub {
            font-weight: 400;
            font-size: 1rem;
        }
        .section-title-header {
            font-weight: 400;
            text-transform: uppercase;
            font-size: 2rem;
            letter-spacing: .1rem;
        }
        .text-primary {
            color : #007eff !important;
        }
    `

    const ContentSection = styled.section`
        width:100%;
        heigh:100%;
    `




    const PortfolioSection = styled.section`
        padding: 15% 0 0;
        text-align: center;
        margin:0 0 15%;
        p {
            margin: 0 0 5px;
        }

        .block-image{
            width:300px;
            height:300px;
            margin:auto;
            border-radius: 50%;
            margin-top:4rem;
            margin-bottom:20px;
            overflow:hidden;
            position: relative;


            img {  
                position:absolute;
                width: 150%;
                top:0;
                left:-90px;

            }
        }

        .block-content{
            max-width: 700px;
            width:100%;
            margin:auto;
            .title-about {
                font-size : 1.5rem;
            }

            hr {
                color: #575757;
            }
            


        }
        
    `


export default App;