import React from 'react'
import styled from 'styled-components'
import Card from '../components/Card'

var experenceData = [
    { id:1, image: './images/logo_html.png', title:'HTML', percentage:"90%", experence:"Advance"},
    { id:2, image: './images/logo_php.png', title: 'PHP', percentage:"70%", experence:"Advance"},
    { id:3, image: './images/logo_mysql.png', title: 'Mysql', percentage:"70%", experence:"Advance"},
    { id:4, image: './images/logo_javascript.png', title: 'Java Script', percentage:"70%", experence:"Advance"},
    { id:5, image: './images/logo_css.png', title: 'CSS', percentage:"60%", experence:'Advance'},
    { id:6, image: './images/logo_nodejs.png', title: 'Nodejs', percentage:"30%", experence: 'Basic'},
    { id:7, image: './images/logo_react.png', title: 'React JS', percentage:"30%",experence: 'Basic'},

];


const Technology = () => {
    return (
        <TechnologySection id="technology" className="container">
            <h3 className="section-title-sub text-primary">Experence</h3>
            <h2 className="section-title-header">Technology</h2>
            <div className="technology-block">
                <div className="row">
                { experenceData.map(item => {
                    return (
                        <Card key={item.id} image={item.image} col="col-lg-4 col-md-6 col-sm-12 col-xs-12 mb-4 mb-lg-5" title={item.title} experence={item.experence}  percentage={item.percentage} />
                    )
                })}
                </div>
            </div>
        </TechnologySection>
    );
}

const TechnologySection = styled.section`
        padding-top: 100px;
        text-align:center;
        max-width: 1100px;

        h2 {
            margin:0 0 3% 0;
        }

        .row {
            display:flex;
            align-items: stretch;
        }

    `

export default Technology;